//////////////////////
// variables
//////////////////////

@list-spaces: none, micro, tiny, small, medium, big, giant; // if you change this change also variables names below
@list-space--none: 0 0; // vertical horizontal
@list-space--micro: .125rem .125rem;
@list-space--tiny: .25rem .25rem;
@list-space--small: .5rem .5rem;
@list-space--medium: 1rem 1rem;
@list-space--big: 2rem 2rem;
@list-space--giant: 4rem 4rem;

//////////////////////
// list
//////////////////////

.list {
  // options
  .list-space(small); // space
  .list--left(); // alignment
  .list--secondary(); // variant
}

.list--block {
  // options
  .list-space(small); // space
  .list--secondary(); // variant
}

// addons

.list--drop {
  .flex-direction(column);
  .flex-wrap(nowrap);
  .list-space(none); // space
  .list--small(); // size
}

// variant

.list--primary {
  .list_btn--default({ .btn--primary() });
}

.list--primary-empty {
  .list_btn--default({ .btn--primary-empty() });
}

.list--secondary {
